export default{
	data() {
		return {
			verification: '获取验证码',
			disabled:false,
			accounts:'',
			verify:'',
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		obtain(){ //获取验证码
			let that = this
			if(!(/^1[3-9][0-9]\d{8,11}$/.test(that.accounts))){
				uni.showToast({
					icon: 'none',
					title: '手机号码格式不正确'
				});
				return;
			}
			if(that.disabled == true) return
			let data = {
				mobile:that.accounts,
				time:Date.parse(new Date())/1000  //时间戳
			}
			console.log(data)
			uni.request({
				url:that.APIconfig.api_root.login.sendPhone,
				method:'POST',
				data,
				success(res) {
					console.log(res)
					let data = res.data 
					console.log(data)
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						that.verification = '60s'
						that.disabled = true
						that.timer = setInterval(function(){
							let num = that.verification.split('s')[0]
							num --
							that.verification = num+'s'
						},1000)
					}
					
				}
			})
			
			
					
		},
		
		login(){
			let that = this
			uni.request({
				url:that.APIconfig.api_root.login.phoneLogin,
				method:'POST',
				data:{
					mobile:that.accounts,
					sms_code:that.verify,
					time:Date.parse(new Date())/1000  //时间戳
				},
				success(res) {
					let data = res.data 
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						uni.setStorageSync('token',JSON.stringify(data.data.memberInfo))










						
						setTimeout(function(){
							
							uni.switchTab({
								url: '../index/index'
							});
						},1000)
					}
				}
			})
		}
		
	},
	watch:{
		verification(curval,oldval){// 监听定时器的num值
			if(curval == '-1s'){
				clearInterval(this.timer)
				this.verification = '重新获取'
				this.disabled = false
			}
		}
	},
}
